@import url(https://fonts.googleapis.com/css2?family=La+Belle+Aurore&display=swap);
:root {
  --border-radius: 15px;
  --main-color: blue;
  background-color: black;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  border: var(--border);
  background-color: #7a7474;
  overflow: hidden;
}
.colorbg{
  height:100vh;
  width: inherit;
  background-color: #7a7474;
}
#message{
    display: -webkit-flex;
    display: flex;
    margin-top: 10px;
    width: 80%;
    background-color: #936756;
    -webkit-flex-direction: column;
            flex-direction: column;
}

#user-info{
    display: -webkit-flex;
    display: flex;
}

.user-image{
    width: 60px;
    height: 60px;
    margin-right: 15px;
    border-radius: 5px;
}

.header span:nth-of-type(1) {
    font-size: 16px;
    color: #72afd3;
}

.header span:nth-of-type(2) {
    font-size: 14px;
}

#content {
    background: whitesmoke;
    margin: 10px 0;
    padding: 5px 15px;
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
    margin-left: 75px;
    border-radius: 5px;
    word-wrap: break-word;
}

#like-button{
    margin-left: 75px;
    margin-top: -20px;
}

#message-container {
    overflow:scroll;
    height: 100%;
}
.home-view {
  overflow: scroll;
  height: 100vh;
}

.draftbox {
  position: -webkit-sticky !important;
  position: sticky !important;

  width: 100vw !important;
  padding: 20px;
  bottom: 50px;
  left: 0;

  z-index: 200;

  background-color: #72afd3;
  border-radius: 5px;
}

.draftbox .field {
  width: 50vw !important;
  margin: auto !important;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-align-items: center;
          align-items: center;
}

.draftbox .field .button {
  height: 3em !important;
  margin-left: 20px;
}

.liked {
  color: #31b7ec !important;
}

.MsgList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
}
#error-page {
    width: 100vw;
    height: 100vh;
    background-color: white;
}

#error-page img {
    height: 100%;
    display: block;
    margin: auto;
}
#login-form {
  padding-top: 150px;
  width: 350px;
  margin: auto;
}

#newaccount-form{
    padding-top: 150px;
    width: 350px;
    margin: auto;
}
.profile {
    padding: 10px;
    position: relative;
}

.profile > h1 {
    text-align: center;
}

.profile-form {
    display: -webkit-flex;
    display: flex;
    width: 450px;
    -webkit-justify-content: center;
            justify-content: center;
    margin: auto;
}

#delete-button {
    background-color: white;
    position: absolute;
    margin: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    top: 0;
    right: 0;
    border-radius: 5px;
}

#delete-button > button {
    margin: 0;
}

hr{
    margin-top: 22px ;
    margin-bottom:22px ;
}

.form {
    width: 85vw;
}

.password-form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    width: 450px;
    margin: auto;
}

.currentImage{
    margin: auto;
    margin-bottom: 20px;
    width: -webkit-max-content;
    width: max-content;
}

.currentImage img{
    margin: auto;
    display: block;
    margin-bottom: 20px;
    width: 300px;
}
#nav-bar{
    padding: 0 4px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-end;
            align-items: flex-end;

    height: 65px;
    background-color: #72afd3;
}

#nav-buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    
    margin: 16px 0;
    height: -webkit-min-content;
    height: min-content;
}

#nav-buttons a:hover, #nav-back-button:hover {
    background: #51646f;
    cursor: pointer;
}

#nav-buttons a {
  color: black;
  padding: calc(1em - 6px) calc(1em + 10px);
  height: -webkit-max-content;
  height: max-content;
  background-clip: padding-box;
    
  background-color: transparent;
}

#nav-buttons a:first-child {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

#nav-buttons a:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

#nav-buttons a:not(:last-child) {
  border-right: 1px solid #5c829b;
}

#nav-back-button {
    color: black;
    margin: 10px 10px;
    padding: 1em;
    height: -webkit-max-content;
    height: max-content;
    
    background-color: transparent;

    border: 1px solid black;
    border-radius: 1.5em;
}

h1.site-title {
  font-family: 'La Belle Aurore' !important;
  font-size: 48px;
  line-height: 2;
  margin: 0;
  width: 200px;
  height: 100%;
  margin-left: 150px;
  text-align: center;
}
.toast {
  position: absolute;
  z-index: 500;
  width: 60vw;
  height: 15vh;
  left: 20vw;
  bottom: 5vh;
  border-radius: 10px;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  background-color: rgba(0,0,0,0);
  opacity: 0;
  visibility: hidden;

  transition: opacity 400ms, background-color 400ms, visibility 400ms;
}

.toast p {
  font-size: 3vh;
  text-align: center;
}

.toast-show {
  opacity: 1;
  visibility: visible;
  background-color: #ffffff; /* default color */
  transition: opacity 800ms, background-color, 800ms, visibility 0ms;
}

/* make custom colors for other error codes here */
.toast-400 {
  background-color: #f35752;
}

.toast-200 {
  background-color: #5dd45f;
}

.toast-1 {
  background-color: #72afd3;
}

.toast-2 {
  background-color: #d18451;
}

.toast-413 {
  background-color: brown;
}
